/* ملف CSS للتنسيقات */
.create-blog {
  max-width: 600px;
  margin: 35px auto;
  padding: 20px;
  background-color: #f9f9f9;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.create-blog h1 {
  text-align: center;
  color: #333;
}

.create-blog form {
  display: flex;
  flex-direction: column;
}

.create-blog div {
  margin-bottom: 15px;
}

.create-blog label {
  font-weight: bold;
  margin-bottom: 5px;
  color: #555;
}

.create-blog input[type="text"],
.create-blog input[type="file"],
.create-blog textarea,
.create-blog select {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}

.create-blog textarea {
  resize: vertical;
  min-height: 150px;
}

.create-blog button {
  padding: 10px 20px;
  background-color: #4CAF50;
  color: white;
  border: none;
  border-radius: 4px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.create-blog button:hover {
  background-color: #45a049;
}

.create-blog p {
  text-align: center;
  color: green;
  font-weight: bold;
}

.create-blog input[type="file"] {
  padding: 5px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.create-blog select {
  padding: 10px;
  border-radius: 4px;
  border: 1px solid #ccc;
  background-color: #fff;
  font-size: 16px;
}

.create-blog select:focus,
.create-blog input:focus,
.create-blog textarea:focus {
  border-color: #4CAF50;
  outline: none;
}